import fetch from './config'
export default {

  /**
   * 
   * @param {object} params 
   * @returns 
   */
  banner(params) {
    return fetch.fetchPost('News/banner', params);
  },

  /**
   * 
   * @param {oeject} params 
   * @returns 
   */
  joke(params) {
    return fetch.fetchPost('joke/index', params);
  },

  /**
   * 
   * @param {object} params {pagetype:}
   * @returns 
   */
  new_list(params) {
    return fetch.fetchGet('News/new_list', params)
  },

  /**
   * 
   * @param {object} params {postid ID}
   * @returns 
   */
  article(params) {
    return fetch.fetchGet('News/new_detail', params)
  },

/**
 * 
 * @param {object} params {ps:pagesize}
 * @returns 
 */
  WxUrl(params) {
    var data = {
      jkAPikey: "d046cd1f569ed13d951f0258902ef9b2"
    }
    params = Object.assign(params, data);
    return fetch.fetchGet('News/new_detail', params)
  },
/**
 * 
 * 
 * @param {object} params {page:10 20 30 ,type:0,1,2}
 * @returns 
 */
  video_type(params){
     return fetch.fetchGet('Video/video_type', params)
  },
/**
 * 
 * @param {any} params 
 * @returns 
 */
 photo(){
    return fetch.fetchGet('Picture/hua_ban');
 }
}


